import { Component, ViewChild } from '@angular/core';
import { Platform, App, LoadingController, AlertController, ToastController, NavController, Nav, Config } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HkApiproviderProvider } from '../providers/hk-apiprovider/hk-apiprovider';
import { LanguageServiceProvider } from '../providers/language-service/language-service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  // @ViewChild('content') navCtrl: NavController;
  validEmail: any;
  rootPage: string = 'HomePage';
  @ViewChild(Nav) nav: Nav;
  // pages: Array<{ title: string, component: any }>;

  apptoken = { "token": "" };

  constructor(
    public platform: Platform,
    public app: App,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public langserv: LanguageServiceProvider,
    public translate: TranslateService,
    public auth: HkApiproviderProvider
  ) {
    this.initializeApp();
    this.apptoken.token = this.auth.gettoken();
    localStorage.setItem('appconfig', JSON.stringify(this.apptoken));


  }

  initializeApp() {
    this.platform.ready().then(() => {
      if (this.platform.is('cordova') || this.platform.is('ios') || this.platform.is('android')) {
        this.statusBar.backgroundColorByHexString('#307722');
        this.splashScreen.hide();
      }
    });
    // moving translation module here to give support while visit direct routes
    this.initTranslate();
  }

  initTranslate() {
    // Set the default language for translation strings, and the current language.
    this.translate.setDefaultLang('en');
    this.translate.setDefaultLang(this.langserv.defaultlang);

    // this.translate.get(['BACK_BUTTON_TEXT']).subscribe(values => {
    //   this.config.set('ios', 'backButtonText', values.BACK_BUTTON_TEXT);
    // });
  }

  onclickHome() {
    this.nav.setRoot('HomePage');
  }

  onclickcategory() {
    this.nav.push('CategoryPage');
  }

  onclickMyOrder() {
    this.nav.push('OrderHistoryPage');
  }

  openContact() {
    this.nav.push('ContactPage');
  }

  onclickNoti() {
    this.nav.push('NotificationsPage');
  }

  openfeedback() {
    this.nav.push('FeedbackPage');
  }

  openoffers() {
    this.nav.push('OffersPage');
  }

  openterms() {
    this.nav.push('TermsPage');
  }

  openprivacy() {
    this.nav.push('TermsPage');
  }

  openIntroPage() {
    this.nav.push('IntroPage');
  }
  checkLogin() {
    if (localStorage.getItem('userData')) {
      return true;
    }
    return false;
  }
}



